import { CCol, CHeaderBrand, CRow, CButton, CNavbar } from "@coreui/react"
import { cilApps, cilBriefcase, cilGrid, cilPeople, cilPlus, cilTask, cilVerticalAlignCenter } from "@coreui/icons"
import CIcon from "@coreui/icons-react"
import { Outlet, NavLink, useParams } from "react-router-dom"
export const Commessa = () => {
    const { id_commessa } = useParams()
    const base_route = `/Commesse/${id_commessa}/`

    const menuItems = [
        {
        text: "Panoramica",
        icon: cilApps,
        to: base_route+''
      },
        {
        text: "Utenti",
        icon: cilPeople,
        to: base_route+'users'
      },
        {
        text: "Punti Luce",
        icon: cilVerticalAlignCenter,
        to: base_route+'punti_luce'
      },
        {
        text: "Azioni di Gruppo",
        icon: cilGrid,
        to: base_route+'group_actions'
      },
        {
        text: "Check e validazione",
        icon: cilTask,
        to: base_route+'check_and_validation'
      }
      ]
return (
<>
  <CRow className="justify-content-between py-3">
    <CCol>
      <CHeaderBrand className='size-header weight-600'><CIcon size='lg' icon={cilBriefcase}/>&nbsp;{id_commessa}</CHeaderBrand>
    </CCol>
    <CCol className="d-flex justify-content-end px-0">
      <CCol className="d-flex justify-content-end px-2">
        <CButton shape="rounded-1 primary border-0 size-text d-flex align-items-center shadow-sm mx-2 weight-600"><CIcon size='lg' icon={cilPlus} />Nuovo Punto Luce</CButton> 
        <CButton shape="rounded-1 contrast color-base border-0 size-text d-flex align-items-center shadow-sm weight-600">Azioni</CButton> 
      </CCol>
    </CCol>
  </CRow>
  <CRow className="m-0">
  <CNavbar className="contrast rounded-1 w-100 d-flex justify-content-start rounded-1 px-2 m-0" expand='lg' colorScheme='light'>
        {menuItems.map(item => 
          (
            <NavLink className={({isActive, isPending}) => `${isActive? "color-highlight" : "color-base"} size-text weight-600 d-flex align-items-center p-2 mx-1 nav-link`} to={item.to}>
              <CIcon icon={item.icon} />&nbsp; {item.text}
            </NavLink>
        ))}
  </CNavbar>
  </CRow>
  <CRow className="m-0">
    <Outlet id_commessa={id_commessa}/>
  </CRow>
</>
)
}
