
class MagneticTesting {
    constructor(oldData = {}){
        this.data = [
            [
                {
                    label: "Marca/Modello",
                    type : "select",
                    options : ["GILARDONI"]
                },
                {
                    label: "Tipologia/Corrente",
                    type : "select",
                    options : [" GILARDONI/TG DOPPIA"]
                }
            ],
            [
                {
                    label: "Attrezzature Ausiliarie",
                    type : "input",
                    placeholder : "Blocco di Calibrazione a Gradini"
                },
                {
                    label : "Frequenza/Dimensione/Angolo",
                    type : "input",
                    placeholder : "4 MHz/10 mm/0"
                }
            ]
        ]
    }
}

export default MagneticTesting