import React from 'react'
import { useState } from 'react'
import CIcon from '@coreui/icons-react'
import { cilCaretBottom, cilCaretTop, cilMinus, cilPenAlt, cilPlus, cilVerticalAlignCenter } from '@coreui/icons'
import { CFormCheck, CHeaderBrand, CContainer, CRow, CCol, CForm, CFormInput, CFormSelect, CButton, CCollapse, CCard, CCardBody, CNavbar, CInputGroup, CInputGroupText } from '@coreui/react'
import { CDatePicker } from '@coreui/react-pro'
import { useParams, NavLink } from 'react-router-dom'
import DatiGenerali from '../../Forms/puntoLuce/dati_generali'
import VisualTesting from '../../Forms/puntoLuce/visual_testing'
import UltrasonicTesting from '../../Forms/puntoLuce/ultrasonic_testing'
import MagneticTesting from '../../Forms/puntoLuce/magnetic_testing'

export const PuntoLuce = (props) => {
    const { id_pl} = useParams()

    const menuItems = [
        {
        text: "Dati Generali",
        to: '#dati_generali'
      },
        {
        text: "Visual Test",
        to: '#visual_testing'
      },
        {
        text: "Ultrasonic Test",
        to: '#ultrasonic_test'
      },
        {
        text: "Magnetic Test",
        to: '#magnetic_test'
      },
        {
        text: "Ver. Strutturale",
        to: '#ver_strutturale'
      },
        {
        text: "Ver. Elettromeccanica",
        to: '#ver_elettromeccanica'
      },
        {
        text: "Note",
        to: '#note'
      },
        {
        text: "Ver. Rettilineit",
        to: '#ver_rettilineit'
      },
        {
        text: "Attestato",
        to: '#attestato'
      }
      ]
    const voci = [
        "Controllo del corretto aggancio dellelemento di sicurezza",
        "Controllo del funzionamento della serratura della portella", 
        "Controllo del fissaggio dei cavi in acciaio e che questi non siano attorcigliati allinterno della torre"
    ]
    const [notes, setNotes] = useState([
        "Nota #1"
    ])
    const formSections = [
        {
            title : "Dati generali della torre",
            data : new DatiGenerali().data
        },
        {
            title : "Visual testing",
            data : new VisualTesting().data
        },
        {
            title : "Ultrasonic testing",
            data : new UltrasonicTesting().data
        },
        {
            title : "Magnetic Testing",
            data : new MagneticTesting().data
        }
    ]
    const [visibleTop, setVisibleTop] = useState(false)

    const createForm = data => data.map( (row, index) => (
    <CRow key={index} className='w-100 justify-content-center mt-1'>
        {row.map(col =>
            <CCol key={col.label}>
                {col.type !== "check" && col.type !== "multi-check" && col.type !== "title" ? <div className='size-text color-base weight-600 mb-2'>{col.label}</div> : <></>}
                {col.type === 'select'?
                    (<CFormSelect aria-label="Default select example" className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'>
                        {col.options.map(option => 
                            <option className='size-text color-base'>{option}</option>
                            )}
                    </CFormSelect>)
                    : col.type === 'input'?
                        (<CFormInput
                            placeholder={col.placeholder}
                            className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'
                            />)
                    : col.type === 'title'?
                        (<div className='size-header color-base weight-700 mb-2'>{col.label}</div>)
                    : col.type === 'file'?
                        (<CFormInput
                            type="file"
                            className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'
                            />)
                    : col.type === 'check'?
                        (
                            <CFormCheck className='size-subHeader color-base weight-600 mb-2 rounded-1' label={col.label}/>
                            )
                    : col.type === 'multi-check'?
                        (<>
                            <CFormCheck className='size-subHeader color-base weight-600 mb-2 rounded-1' label={col.label}/>
                            {col.checks.map(check =>
                                <CFormCheck className='size-text color-base weight-400 mb-2 mx-4 rounded-1' label={check}/>
                                )}
                        </>)
                    : col.type === 'date'?
                        (<CDatePicker
                            id="date"
                            className='w-75'
                            locale='It-it'
                            size="sm"
                            footer
                            />)
                    : <div></div>
                }
            </CCol>
        )}
    </CRow>
    )
    )
    return (
    <>
    <CContainer className="my-3 contrast justify-content-center rounded-1">
        <CNavbar className="contrast rounded-1 w-100 d-flex justify-content-start rounded-1 m-0" expand='lg' colorScheme='light'>
                {menuItems.map(item => 
                (
                    <NavLink className={`size-text weight-600 d-flex align-items-center py-2 mx-1 nav-link`} to={item.to}>
                        <CIcon icon={item.icon} />&nbsp; {item.text}
                    </NavLink>
                ))}
        </CNavbar>
    </CContainer>
    <CContainer className="my-3 p-4 contrast justify-content-center rounded-1">
        <CCol className='d-flex justify-content-start mb-3'>
            <CHeaderBrand className='size-header weight-600 d-flex align-items-center'><CIcon size='lg' className='color-highlight' icon={cilVerticalAlignCenter}/>&nbsp;{id_pl}</CHeaderBrand>
        </CCol>
        {formSections.map(section =>(
            <CRow className='m-0 w-100 justify-content-center my-2'>
            <CButton className='px-4 py-3 d-flex justify-content-between primary-light border-0 color-black size-text weight-600' href="#" onClick={(event) => {
                event.preventDefault()
                setVisibleTop(!visibleTop)
            }}>
            <div>{section.title}</div>
            {visibleTop? <CIcon icon={cilCaretTop}/> : <CIcon icon={cilCaretBottom}/>}
            </CButton>
            <CCollapse className='p-0' visible={visibleTop}>
                <CCard className='background-light border-0'>
                    <CCardBody>
                    <CForm className="justify-content-center rounded-1 px-3 py-2">
                        {createForm(section.data)}
                    </CForm>
                    </CCardBody>
                </CCard>
            </CCollapse>
            </CRow>
        ))}
            <CRow className='m-0 w-100 justify-content-center my-2'>
            <CButton className='px-4 py-3 d-flex justify-content-between primary-light border-0 color-black size-text weight-600' href="#" onClick={(event) => {
                event.preventDefault()
                setVisibleTop(!visibleTop)
            }}>
            <div>Verifica Strutturale</div>
            {visibleTop? <CIcon icon={cilCaretTop}/> : <CIcon icon={cilCaretBottom}/>}
            </CButton>
            <CCollapse className='p-0' visible={visibleTop}>
                <CCard className='background-light border-0'>
                    <CCardBody>
                    <CForm className="justify-content-center rounded-1 px-3 py-2">
                        <CRow>
                            <CCol>
                                <div className='size-text color-base weight-600 mb-2'>Scegli Modello</div>
                                <CFormSelect
                                className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'
                                >
                                  <option className='size-text color-base'>Torri 2 Tronchi</option>
                                </CFormSelect>
                                <div className='size-text color-base weight-700 my-2'>SEZIONE DI TESTA <CIcon size='sm' icon={cilPenAlt}/></div>
                                <div className='size-text color-base weight-700 mb-2'>SEZIONE SECONDO INNESTO <CIcon size='sm' icon={cilPenAlt}/></div>
                                <div className='size-text color-base weight-700 mb-2'>SEZIONE PRIMO INNESTO <CIcon size='sm' icon={cilPenAlt}/></div>
                                <div className='size-text color-base weight-700 mb-2'>SEZIONE BASE <CIcon size='sm' icon={cilPenAlt}/></div>
                            </CCol>
                            <CCol>
                                <div className='size-text color-base weight-600 mb-2'>Anteprima Disegno Grafico</div>
                                img
                            </CCol>
                        </CRow>
                    </CForm>
                    </CCardBody>
                </CCard>
            </CCollapse>
            </CRow>
            <CRow className='m-0 w-100 justify-content-center my-2'>
            <CButton className='px-4 py-3 d-flex justify-content-between primary-light border-0 color-black size-text weight-600' href="#" onClick={(event) => {
                event.preventDefault()
                setVisibleTop(!visibleTop)
            }}>
            <div>Verifica Elettromeccanica</div>
            {visibleTop? <CIcon icon={cilCaretTop}/> : <CIcon icon={cilCaretBottom}/>}
            </CButton>
            <CCollapse className='p-0' visible={visibleTop}>
                <CCard className='background-light border-0'>
                    <CCardBody>
                    <CForm className="justify-content-center rounded-1 px-3 py-2">
                        <CRow>
                            <CCol>
                                
                            </CCol>
                            <CCol className=' d-flex justify-content-end'>
                                <CButton shape="rounded-1 primary border-0 size-text d-flex align-items-center shadow-sm mx-2 weight-600"><CIcon size='lg' icon={cilPlus} />Aggiungi voce</CButton> 
                            </CCol>
                        </CRow>
                        <CRow>
                            <div className='size-subHeader color-base weight-700'>Riepilogo:</div>
                            <ul className='mx-2 mt-1'>
                                {voci.map(voce =>
                                    <li className='mx-4 size-text'>{voce}</li>
                                    )}
                            </ul>
                        </CRow>
                    </CForm>
                    </CCardBody>
                </CCard>
            </CCollapse>
            </CRow>
            <CRow className='m-0 w-100 justify-content-center my-2'>
            <CButton className='px-4 py-3 d-flex justify-content-between primary-light border-0 color-black size-text weight-600' href="#" onClick={(event) => {
                event.preventDefault()
                setVisibleTop(!visibleTop)
            }}>
            <div>Note - commenti- anomalie</div>
            {visibleTop? <CIcon icon={cilCaretTop}/> : <CIcon icon={cilCaretBottom}/>}
            </CButton>
            <CCollapse className='p-0' visible={visibleTop}>
                <CCard className='background-light border-0'>
                    <CCardBody>
                    <CForm className="justify-content-center rounded-1 px-3 py-2">
                        <CRow>
                            <CCol>
                                <div className='size-subHeader color-base weight-700'>Note</div>
                                {notes.map((note, index) =>
                                    <div className='d-flex my-2'>
                                        <CFormInput
                                        placeholder={note}
                                        className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'
                                        />
                                        {index !== 0 && <CButton onClick={() => setNotes(notes.filter((oldNote, nodeIndex) => nodeIndex !== index))} shape="rounded-1 primary border-0 size-text d-flex align-items-center shadow-sm mx-2 weight-600 px-2"><CIcon size='sm' icon={cilMinus}/></CButton>}
                                        {index === notes.length -1 && <CButton onClick={() => setNotes([...notes, `Nota # ${notes.length + 1}`])} shape="rounded-1 primary border-0 size-text d-flex align-items-center shadow-sm mx-2 weight-600 px-2"><CIcon size='sm' icon={cilPlus}/></CButton>}
                                    </div>
                                    )}
                            </CCol>
                            <CCol>
                                {
                                    <ul className='mt-1'>
                                    {notes.map(note =>
                                        <li className='size-text'>{note}</li>
                                        )}
                                    </ul>
                                }
                            </CCol>
                        </CRow>
                    </CForm>
                    </CCardBody>
                </CCard>
            </CCollapse>
            </CRow>
            <CRow className='m-0 w-100 justify-content-center my-2'>
            <CButton className='px-4 py-3 d-flex justify-content-between primary-light border-0 color-black size-text weight-600' href="#" onClick={(event) => {
                event.preventDefault()
                setVisibleTop(!visibleTop)
            }}>
            <div>Verifica Rettilineit</div>
            {visibleTop? <CIcon icon={cilCaretTop}/> : <CIcon icon={cilCaretBottom}/>}
            </CButton>
            <CCollapse className='p-0' visible={visibleTop}>
                <CCard className='background-light border-0'>
                    <CCardBody>
                    <CForm className="justify-content-center rounded-1 px-3 py-2">
                        <CRow>
                            <CCol>
                                <div className='size-subHeader color-base weight-700'>Diametro (mm)</div>
                                <CInputGroup className='rounded-1 w-75 my-2'>
                                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>Tirafondo n.1</CInputGroupText>
                                    <CFormInput
                                        placeholder="24"
                                        className='size-text color-base weight-400 py-1 border-light'
                                        />
                                </CInputGroup>
                                <CInputGroup className='rounded-1 w-75 my-2'>
                                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>Tirafondo n.2</CInputGroupText>
                                    <CFormInput
                                        placeholder=""
                                        className='size-text color-base weight-400 py-1 border-light'
                                        />
                                </CInputGroup>
                                <CInputGroup className='rounded-1 w-75 my-2'>
                                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>n</CInputGroupText>
                                    <CFormInput
                                        placeholder=""
                                        className='size-text color-base weight-400 py-1 border-light'
                                        />
                                </CInputGroup>
                            </CCol>
                            <CCol>
                                <div className='size-subHeader color-base weight-700'>Zona di misura</div>
                                <CInputGroup className='rounded-1 w-75 my-2'>
                                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>0</CInputGroupText>
                                    <CFormInput
                                        placeholder="24"
                                        className='size-text color-base weight-400 py-1 border-light'
                                        />
                                </CInputGroup>
                                <CInputGroup className='rounded-1 w-75 my-2'>
                                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>90</CInputGroupText>
                                    <CFormInput
                                        placeholder=""
                                        className='size-text color-base weight-400 py-1 border-light'
                                        />
                                </CInputGroup>
                                <CInputGroup className='rounded-1 w-75 my-2'>
                                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>180</CInputGroupText>
                                    <CFormInput
                                        placeholder=""
                                        className='size-text color-base weight-400 py-1 border-light'
                                        />
                                </CInputGroup>
                                <CInputGroup className='rounded-1 w-75 my-2'>
                                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>360</CInputGroupText>
                                    <CFormInput
                                        placeholder=""
                                        className='size-text color-base weight-400 py-1 border-light'
                                        />
                                </CInputGroup>
                            </CCol>
                        </CRow>
                    </CForm>
                    </CCardBody>
                </CCard>
            </CCollapse>
            </CRow>
    </CContainer>
    </>
    )
}
